import React, { Component } from "react";
import { Formik } from "formik";
import * as Yup from "yup";
import axios from "axios";
import { Row, Container} from 'react-bootstrap'
import swal from "sweetalert";
const PasswordresetSchema = Yup.object().shape({
  password: Yup.string().required("New Password is required"),
  confirm_password: Yup.string().oneOf(
    [Yup.ref("password"), null],
    "Both password need to be the same"
  )
});

class Passwordreset extends Component {
  constructor(props) {
    super(props);

    this.state = {
      response: {},
      error_message: null,
      avatar: ""
    };
  }

  submitForm = async (values, history, token) => {
    await axios
      .put("http://localhost:8080/password/reset?token=" + token, values)
      .then(res => {
        if (res.data.result === "success") {
          swal("Success!", res.data.message, "success").then(value => {
            history.push("/login");
          });
        } else if (res.data.result === "error") {
          swal("Error!", res.data.message, "error");
        }
      })
      .catch(error => {
        console.log(error);
        swal("Error!", "Unexpected error", "error");
      });
  };
  showForm = ({
    values,
    errors,
    touched,
    handleChange,
    handleSubmit,
    onSubmit,
    isSubmitting,
    setFieldValue
  }) => {
    return (
      <div>
      <Row className="justify-content-center">
      <h1><b>Last</b>Play<span style={{fontSize:"14px"}}>.tv</span></h1> 
     
      </Row>
      <Row className="justify-content-center">
      <img src={process.env.PUBLIC_URL + '/logo100.png'} alt="logo" /> 
      </Row>
      <form style={{marginTop:"5%"}} onSubmit={handleSubmit}>
      <span style={{textAlign:"Center"}} >You have requested to change your password. Please enter your new password below.</span>

      <Row className="justify-content-center">

            <input
              name="password"
              onChange={handleChange}
              value={values.password}
              type="password"
              className={
                errors.password && touched.password
                  ? "form-control is-invalid"
                  : "form-controls2"
              }
              id="password"
              placeholder="Enter new password"
            />
            {errors.password && touched.password ? (
              <small id="passwordHelp" class="text-danger">
                {errors.password}
              </small>
            ) : null}
            </Row>
                <Row className="justify-content-center">


            <input
              onChange={handleChange}
              value={values.confirm_password}
              type="password"
              className={
                errors.confirm_password && touched.confirm_password
                  ? "form-control is-invalid"
                  : "form-controls2"
              }
              id="confirm_password"
              name="confirm_password  "
              placeholder="Enter password again"
            />
            {errors.confirm_password && touched.confirm_password ? (
              <small id="passwordHelp" class="text-danger">
                {errors.confirm_password}
              </small>
            ) : null}
</Row>
        </form>
<Row className="justify-content-center">
<button
              type="submit"
              disabled={isSubmitting}
              className="btn btn-primary"
              style={{marginTop:'25px'}}
            >
              Send
            </button>
        </Row>
      </div>
    );
  };

  render() {
    return (
<Container>
              <Formik
                initialValues={{
                  password: ""
                }}
                onSubmit={(values, { setSubmitting }) => {
                  this.submitForm(values, this.props.history,this.props.match.params["token"]
                  );
                  setSubmitting(false);
                }}
                validationSchema={PasswordresetSchema}
              >
                {/* {this.showForm()}            */}
                {props => this.showForm(props)}
              </Formik>
</Container>
    
    )}
}

export default Passwordreset;